.loading{display:flex;align-items:center}.page{background-color:#f2f5f7;padding:20px 250px;display:grid;gap:10px;min-height:100vh;grid-auto-rows:max-content}.page__breadcrumb{padding:10px 0;list-style:none;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.page__breadcrumb li{font-family:Raleway;font-size:16px;font-weight:400;line-height:16px}.page__breadcrumb li+li:before{padding:8px;color:#000;content:"/ "}.page__breadcrumb li>a{color:#8a8a8a}.page__breadcrumb li>a:hover{color:#f05a00}.page__title{display:flex;justify-content:space-between}.page__title h1{font-family:Raleway;font-size:40px;font-weight:400;line-height:1.2;text-transform:uppercase}.page__title button{border:1px solid #dedbdb;background-color:#f2f5f7;padding:14px 32px}.page__title button>a{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#000}.head{background-color:#333;display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:16px 250px}.head__logo>a{color:#fff}.head__nav{position:relative;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.head__nav__link{position:relative}.head__nav__img{width:32px;height:32px;padding:16px;fill:none;stroke:#fff}.head__nav__img:hover{background-color:#3b3b3b}.head__nav div{position:absolute;right:0;top:5px;display:flex;flex-direction:row;align-items:center;justify-content:center;color:#fff;width:23px;height:23px;border-radius:50px;background-color:#f05a00}.categories{background-color:#000;padding:10px 250px}.categories button{background-color:rgba(0,0,0,0);border:1px solid rgba(0,0,0,0);color:#fff;font-weight:500;padding:10px;margin:0 5px;border-radius:5px}.categories button:hover{border-color:#f05a00}.categories button.btn-active{border:1px solid #f05a00;box-shadow:1px 1px 5px #f05a00}.categories button a:visited,.categories button a:active{color:#fff}