.loading{display:flex;align-items:center}.page{background-color:#f2f5f7;padding:20px 250px;display:grid;gap:10px;min-height:100vh;grid-auto-rows:max-content}.page__breadcrumb{padding:10px 0;list-style:none;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.page__breadcrumb li{font-family:Raleway;font-size:16px;font-weight:400;line-height:16px}.page__breadcrumb li+li:before{padding:8px;color:#000;content:"/ "}.page__breadcrumb li>a{color:#8a8a8a}.page__breadcrumb li>a:hover{color:#f05a00}.page__title{display:flex;justify-content:space-between}.page__title h1{font-family:Raleway;font-size:40px;font-weight:400;line-height:1.2;text-transform:uppercase}.page__title button{border:1px solid #dedbdb;background-color:#f2f5f7;padding:14px 32px}.page__title button>a{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#000}.home{background-color:#f2f5f7;padding:20px 250px}.loading{display:flex;flex-direction:row;align-items:center;justify-content:center;flex-wrap:wrap;gap:10px}