.loading{display:flex;align-items:center}.page{background-color:#f2f5f7;padding:20px 250px;display:grid;gap:10px;min-height:100vh;grid-auto-rows:max-content}.page__breadcrumb{padding:10px 0;list-style:none;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.page__breadcrumb li{font-family:Raleway;font-size:16px;font-weight:400;line-height:16px}.page__breadcrumb li+li:before{padding:8px;color:#000;content:"/ "}.page__breadcrumb li>a{color:#8a8a8a}.page__breadcrumb li>a:hover{color:#f05a00}.page__title{display:flex;justify-content:space-between}.page__title h1{font-family:Raleway;font-size:40px;font-weight:400;line-height:1.2;text-transform:uppercase}.page__title button{border:1px solid #dedbdb;background-color:#f2f5f7;padding:14px 32px}.page__title button>a{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#000}.product{display:grid;grid-template-columns:50% 50%;justify-content:center;background-color:#fff;padding:60px;gap:20px}.product__image{display:flex;flex-direction:column;align-items:center;justify-content:center}.product__image__container{position:relative;height:550px;display:flex;flex-direction:column;align-items:center;justify-content:center}.product__image__container .actions{position:absolute;top:50%;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between}.product__image__container .actions button{margin:0 10px;background-color:#f2f5f7;border:none;border-radius:50%;display:flex;flex-direction:row;align-items:center;justify-content:center;cursor:pointer}.product__image__container .actions button:hover{background-color:#dedbdb}.product__image__container .actions__icon{width:20px;height:26px;fill:none;stroke:#000}.product__image--main{object-fit:contain;max-width:100%}.product__thumbnail{display:flex;flex-direction:row;align-items:center;justify-content:center;padding:20px;text-wrap:nowrap;overflow-x:auto;width:100%}.product__thumbnail::-webkit-scrollbar{width:3px;height:5px}.product__thumbnail::-webkit-scrollbar-track{background:rgba(0,0,0,0)}.product__thumbnail::-webkit-scrollbar-thumb{background:rgba(0,0,0,.2);box-shadow:inset 1px 1px 0px rgba(0,0,0,.1),inset 0px -1px 0px rgba(0,0,0,.07)}.product__thumbnail::-webkit-scrollbar-thumb:hover{background:rgba(0,0,0,.4);box-shadow:inset 1px 1px 1px rgba(0,0,0,.25)}.product__thumbnail .active{transform:scale(1.2);box-shadow:0px 0px 20px -3px #f05a00}.product__thumbnail img{margin:10px;transition:all 500ms;cursor:pointer;box-shadow:0px 15px 20px 0px rgba(0,0,0,.15)}.product__content{display:flex;flex-direction:column;align-items:flex-start;justify-content:center}.product__content__title{font-family:Raleway;font-size:30px;font-weight:400;line-height:32px}.product__content__stock--available{font-family:Raleway;font-size:16px;font-weight:400;line-height:20px;margin:20px 0;color:green}.product__content__stock--unavailable{font-family:Raleway;font-size:16px;font-weight:400;line-height:20px;margin:20px 0;color:red}.product__content__description{font-family:Raleway;font-size:16px;font-weight:400;line-height:20px}.product__content__price{font-family:Raleway;font-size:30px;font-weight:400;line-height:32px;margin:40px 0 60px 0}.product__content__btns{display:flex;flex-direction:row;align-items:center;justify-content:center;gap:20px}