.loading{display:flex;align-items:center}.page{background-color:#f2f5f7;padding:20px 250px;display:grid;gap:10px;min-height:100vh;grid-auto-rows:max-content}.page__breadcrumb{padding:10px 0;list-style:none;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.page__breadcrumb li{font-family:Raleway;font-size:16px;font-weight:400;line-height:16px}.page__breadcrumb li+li:before{padding:8px;color:#000;content:"/ "}.page__breadcrumb li>a{color:#8a8a8a}.page__breadcrumb li>a:hover{color:#f05a00}.page__title{display:flex;justify-content:space-between}.page__title h1{font-family:Raleway;font-size:40px;font-weight:400;line-height:1.2;text-transform:uppercase}.page__title button{border:1px solid #dedbdb;background-color:#f2f5f7;padding:14px 32px}.page__title button>a{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#000}.cart__list{display:flex;align-items:center;font-family:Raleway;font-size:20px;font-weight:500;line-height:1.2;padding:24px 0}.cart__list li:first-child{width:42%}.cart__list li:nth-child(2),.cart__list li:last-child{width:18%}.cart__list li:nth-child(3){width:22%}.cart__empty{text-align:center;font-size:24px}.cart__empty a{font-family:Raleway;font-size:24px;font-weight:500;line-height:1.2;color:#f05a00}.cart__empty a:link,.cart__empty a:visited{color:#f05a00}.cart__empty a:hover{text-decoration:underline}.cart__card{background-color:#fff;display:flex;align-items:center}.cart__card__title{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2}.cart__card__image{width:60px;height:60px}.cart__card__image-container{display:inline-flex;gap:30px;align-items:center;width:42%;cursor:pointer}.cart__card__price{width:18%}.cart__card__price p{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2}.cart__card__qty{width:22%;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.cart__card__qty span{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;padding:10px;border-left:1px solid #dedbdb;border-right:1px solid #dedbdb}.cart__card__qty button{cursor:pointer;border:none;background:none}.cart__card__qty button:hover>.cart__card__qty-icon{stroke:#f05a00}.cart__card__qty-icon{width:24px;height:24px;stroke:#000}.cart__card__qty__total>span{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2}.cart__card__remove{cursor:pointer;border:none;background:none;margin-left:auto;margin-right:10px}.cart__card__remove:hover>.cart__card__remove-icon{stroke:#f05a00}.cart__card__remove-icon{width:24px;height:24px;stroke:#000}.cart__total{display:flex;flex-direction:column;align-items:normal;justify-content:normal;width:300px;margin-left:auto}.cart__total div{display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:20px 0}.cart__total__price{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#000}.cart__total__discount{font-family:Raleway;font-size:16px;font-weight:500;line-height:1.2;color:#8a8a8a}.cart__total__pay{font-family:Raleway;font-size:22px;font-weight:500;line-height:1.2;color:#000}.cart__total__pay-orange{color:#f05a00}.btn--orange{padding:14px 64px;background:#f05a00;color:#fff;border:none;cursor:pointer}.btn--orange:active{transform:scaleX(1.02)}